html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: block;
  margin: 0;
  padding: 0;

  font-family: 'PT Sans', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
}

.container {
  display: block;
  width: 1028px;
  margin: 0;
  margin: 0 auto;
  padding: 0;
}

.content {
  display: block;
  margin: 0;
  padding: 0;
}

.content__form {
  display: block;
  margin: 0;
  padding: 0;
}

.form__title {
  display: block;
  margin: 0;
  padding: 0;

  font-family: 'PT Sans', 'Arial', sans-serif;
  font-size: 32px;
  line-height: 36px;
  font-weight: 700;
  text-align: center;
}

.form__text {
  display: block;
  margin: 0;
  margin-top: 20px;
  padding: 0;

  font-family: 'PT Sans', 'Arial', sans-serif;
  font-size: 22px;
  line-height: 26px;
  font-weight: 400;
  text-align: center;
}

.from__input {
  display: block;
  width: 330px;
  margin: 0;
  margin: 0 auto;
  margin-top: 10px;
  padding: 0;
  padding: 5px;

  font-family: 'PT Sans', 'Arial', sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  text-align: center;

  border: 1px solid lightslategrey;
}

.form__btn {
  display: block;
  width: 330px;
  margin: 0;
  margin: 0 auto;
  margin-top: 10px;
  padding: 0;
  padding: 8px 0px;

  font-family: 'PT Sans', 'Arial', sans-serif;
  font-size: 18px;
  line-height: 20px;
  font-weight: 400;
  text-align: center;

  background-color: lightsteelblue;

  border: none;
}

.form__btn:hover {
  background-color: lightslategray;
  cursor: pointer;
}

.form__found-comp-output {
  display: block;
  margin: 0;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 0;
}

.form__found-comp {
  margin: 0;
  padding: 0;

  font-family: 'PT Sans', 'Arial', sans-serif;
  font-size: 18px;
  line-height: 20px;
  font-weight: 400;
  text-align: center;
}

.form__found-comp span {
  font-weight: 700;
}

.content__output {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin: 0;
  margin-top: 25px;
  margin-bottom: 25px;
  padding: 0;
}

.output__items {
  display: flex;
  flex-direction: column;
  width: 330px;
  margin: 0;
  margin-top: 9px;
  padding: 0;
  padding: 20px;

  border: 1px solid lightslategrey;

  list-style: none;
  text-decoration: none;
}

.output__item {
  display: block;
  margin: 0;
  margin-top: 5px;
  padding: 0;

  font-family: 'PT Sans', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  text-align: left;
}

.output__item a {
  margin: 0;
  padding: 0;

  color: gray;

  list-style: none;
  text-decoration: none;
}

.output__item a:hover {
  color: tomato;
}

.output__item span {
  font-weight: 700;
}

.output__item--img {
  margin-top: auto;
}

.output__item img {
  display: block;
  margin: 0;
  margin: 0 auto;
  margin-top: 15px;
  padding: 0;
}